import React from 'react';
import PropTypes from 'prop-types';
//
import CountDownUpDiv from '../../../../../../../component/some_div/count_down_up_div/CountDownUpDiv';
//
import './FsItemIfRCount.scss';

//
FsItemIfRCount.propTypes = {};

//
function FsItemIfRCount({
    count,
    total_add_cart,
    max,
    min,
    disabled,

    countUp,
    countDown,
    beforeCountNum,
    countNum,
    countNumDone,
}) {
    return (
        <div className="FsItemIfRCount">
            <div className="display-flex align-items-center">
                <div>
                    <span>Số Lượng</span>
                </div>

                <div className="display-flex align-items-center">
                    <CountDownUpDiv
                        disabled={disabled}
                        count={count}
                        max={max}
                        min={min}
                        //
                        countDown={countDown}
                        countUp={countUp}
                        beforeCountNum={beforeCountNum}
                        countNum={countNum}
                        countNumDone={countNumDone}
                    />

                    <div>
                        <span>{max} products available</span>

                        <span className="margin-left-5px text-red">
                            ({total_add_cart} in cart)
                        </span>
                    </div>
                </div>
            </div>
        </div>
    );
}

export default FsItemIfRCount;
