import React from 'react';
import PropTypes from 'prop-types';
//
import FashionIsLike from '../../../../../components/is_like/FashionIsLike';
//
import './FsItemIfRName.scss';

//
FsItemIfRName.propTypes = {};

//
function FsItemIfRName({ name, is_like }) {
    //
    return (
        <div className="FsItemIfRName">
            <FashionIsLike is_like={is_like} className="FsItemIfRName_like" />

            <h1 className="font-20px label-field">{name}</h1>
        </div>
    );
}

export default FsItemIfRName;
