import React from 'react';
import PropTypes from 'prop-types';
//
import IconsMenu from '../../../../../../../_icons_svg/icons_menu/IconsMenu';
//
import IconDiv from '../../../../../../../component/some_div/icon_div/IconDiv';
//
import './FsItemIfRCart.scss';

//
FsItemIfRCart.propTypes = {
    max: PropTypes.number,
    total_add_cart: PropTypes.number,
    wait_add_cart: PropTypes.bool,
    addToCart: PropTypes.func,
};

//
function FsItemIfRCart({ max, wait_add_cart, addToCart }) {
    //
    return (
        <div className="FsItemIfRCart">
            <div className="display-flex align-items-center">
                <button
                    type="button"
                    className={`FsItemIfRCart_add display-flex-center btn btn-active btn-hv brs-5px label-field ${
                        wait_add_cart ? 'cursor-wait' : 'cursor-pointer'
                    }`}
                    disabled={max == 0 || wait_add_cart}
                    onClick={addToCart}
                >
                    <IconDiv Icon={IconsMenu} x={400}>
                        <span>thêm vào giỏ hàng</span>
                    </IconDiv>
                </button>

                <button className="FsItemIfRCart_buy display-flex-center btn btn-hv btn-active brs-5px label-field">
                    <div>
                        <span className="text-white">Mua ngay</span>
                    </div>
                </button>
            </div>
        </div>
    );
}

export default FsItemIfRCart;
